:root {
  --font-body: "Barlow Semi Condensed", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --moderate-violet: hsl(263, 55%, 52%);
  --very-dark-grayish-blue: hsl(217, 19%, 35%);
  --very-dark-blackish-blue: hsl(219, 29%, 14%);
  --light-gray: hsl(0, 0%, 81%);
  --light-grayish-blue: hsl(210, 46%, 95%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--light-grayish-blue);
  color: var(--white);
  font-weight: 500;
  font-size: 0.8125rem;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 2.25rem;
  letter-spacing: 0.125rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

.container {
  padding: 1.5rem;
  max-width: 69.6875rem;
  margin: 0 auto;
  display: grid;
  place-content: center;
  gap: 1.875rem;
  min-height: 100vh;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-areas: "col1 col1 col2 col5" "col3 col4 col4 col5";
  }
  .container__col1 {
    grid-area: col1;
  }
  .container__col2 {
    grid-area: col2;
  }
  .container__col3 {
    grid-area: col3;
  }
  .container__col4 {
    grid-area: col4;
  }
  .container__col5 {
    grid-area: col5;
  }
}

.card {
  border-radius: 5px;
  padding: 30px;
}
.card--user1 {
  background-color: var(--moderate-violet);
}
.card--user2 {
  background-color: var(--very-dark-grayish-blue);
}
.card--user3 {
  background-color: var(--white);
}
.card--user4 {
  background-color: var(--very-dark-blackish-blue);
}
.card--user5 {
  background-color: var(--white);
}
.card__icon {
  border-radius: 50%;
}
.card__username {
  color: var(--white);
  font-weight: 600;
  letter-spacing: 0.01875rem;
}
.card__username--color {
  color: var(--very-dark-grayish-blue);
}
.card__username--opac {
  opacity: 50%;
}
.card__header {
  font-size: 1.25rem;
  margin: 1.25rem 0;
  font-weight: 600;
  line-height: 1.2;
  color: var(--white);
  letter-spacing: 0.025rem;
}
.card__header--color {
  color: var(--very-dark-grayish-blue);
}
.card__body {
  line-height: 1.5;
  letter-spacing: 0.01875rem;
  color: var(--white);
}
.card__body--color {
  color: var(--very-dark-grayish-blue);
}
.card__body--opac {
  opacity: 70%;
}

.flex {
  display: flex;
  gap: 0.9375rem;
}
.flex__jc {
  justify-content: start;
}
.flex__ai {
  align-items: center;
}